"use client";

import { useState, useRef, useEffect } from "react";
import { v4 as uuidv4 } from 'uuid';

// Create a simple hook to replace the missing useScrollToBottom
function useCustomScrollToBottom() {
  const containerRef = useRef(null);
  const endRef = useRef(null);
  
  const scrollToBottom = () => {
    if (endRef.current) {
      endRef.current.scrollIntoView({ behavior: "smooth" });
    }
  };
  
  useEffect(() => {
    scrollToBottom();
  }, []);
  
  return [containerRef, endRef, scrollToBottom];
}

// Component definitions that would normally be imported
const ChatInput = ({ question, setQuestion, onSubmit, isLoading }) => {
  const handleSubmit = (e) => {
    e.preventDefault();
    onSubmit();
  };

  return (
    <form onSubmit={handleSubmit} className="w-full">
      <div className="flex gap-2">
        <input
          type="text"
          value={question}
          onChange={(e) => setQuestion(e.target.value)}
          placeholder="Type your message here..."
          className="flex-1 border rounded p-2"
          disabled={isLoading}
        />
        <button 
          type="submit" 
          disabled={isLoading || !question.trim()} 
          className="bg-blue-500 text-white rounded p-2 hover:bg-blue-700 disabled:bg-gray-300"
        >
          Send
        </button>
      </div>
    </form>
  );
};

const PreviewMessage = ({ message }) => {
  return (
    <div className={`p-4 rounded-lg max-w-3xl mx-auto w-full ${message.role === 'user' ? 'bg-blue-100' : 'bg-gray-100'}`}>
      <div className="font-bold">{message.role === 'user' ? 'You' : 'Assistant'}</div>
      <div className="mt-1">{message.content}</div>
      {message.sources && (
        <div className="mt-2 text-sm text-gray-600">
          <div>Sources: {message.sources.join(', ')}</div>
        </div>
      )}
    </div>
  );
};

const ThinkingMessage = () => {
  return (
    <div className="p-4 rounded-lg max-w-3xl mx-auto w-full bg-gray-100">
      <div className="font-bold">Assistant</div>
      <div className="mt-1">Thinking...</div>
    </div>
  );
};

const Overview = () => {
  return (
    <div className="text-center p-4 max-w-3xl mx-auto">
      <h2 className="text-2xl font-bold mb-4">Welcome to Chat</h2>
      <p>This is a simple chat application. Start by sending a message below!</p>
    </div>
  );
};

const Header = () => {
  return (
    <header className="bg-white shadow p-4">
      <h1 className="text-xl font-bold">Chat Application</h1>
    </header>
  );
};

const CHAT_HTTP_URL = "http://localhost:5000/chat"; // Your chat HTTP endpoint
const UPLOAD_HTTP_URL = "http://localhost:5000/upload"; // Your upload HTTP endpoint

export function Chat() {
  const [containerRef, endRef, scrollToBottom] = useCustomScrollToBottom();
  const [messages, setMessages] = useState([]);
  const [question, setQuestion] = useState("");
  const [isLoading, setIsLoading] = useState(false);
  const isWaitingForResponse = useRef(false);
  const [selectedFile, setSelectedFile] = useState(null);
  const [uploadStatus, setUploadStatus] = useState("");

  async function handleSubmit(text) {
    if (isLoading || isWaitingForResponse.current) return;

    const messageText = text || question;
    if (!messageText.trim()) return;
    
    setIsLoading(true);
    isWaitingForResponse.current = true;
    const traceId = uuidv4();
    setMessages(prev => [...prev, { content: messageText, role: "user", id: traceId }]);
    setQuestion("");

    try {
      const response = await fetch(CHAT_HTTP_URL, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ message: messageText }),
      });

      if (!response.ok) {
        const errorData = await response.json();
        setMessages(prev => [...prev, { content: `Error: ${errorData?.error || 'Failed to fetch response'}`, role: "assistant", id: uuidv4() }]);
      } else {
        const data = await response.json();
        setMessages(prev => [...prev, { content: data.response, role: "assistant", id: uuidv4(), sources: data.sources, bloom_level: data.bloom_level }]);
      }
    } catch (error) {
      console.error("Error fetching chat response:", error);
      setMessages(prev => [...prev, { content: `Error: ${error.message || 'An unexpected error occurred'}`, role: "assistant", id: uuidv4() }]);
    } finally {
      setIsLoading(false);
      isWaitingForResponse.current = false;
      // Scroll to bottom after new message
      setTimeout(scrollToBottom, 100);
    }
  }

  const handleFileChange = (event) => {
    if (event.target.files && event.target.files.length > 0) {
      setSelectedFile(event.target.files[0]);
    }
  };

  const uploadFile = async () => {
    if (!selectedFile) {
      setUploadStatus("Please select a file");
      return;
    }

    const formData = new FormData();
    formData.append('file', selectedFile);

    try {
      setUploadStatus("Uploading...");
      const response = await fetch(UPLOAD_HTTP_URL, {
        method: 'POST',
        body: formData,
      });

      if (!response.ok) {
        const errorData = await response.json();
        setUploadStatus(errorData?.error || 'File upload failed');
      } else {
        const data = await response.json();
        setUploadStatus(data?.message || 'File uploaded successfully');
        // Optionally clear the selected file after successful upload
        setSelectedFile(null);
        // Optionally reset the upload status after a short delay
        setTimeout(() => setUploadStatus(""), 3000);
      }
    } catch (error) {
      console.error("Error uploading file:", error);
      setUploadStatus(error.message || 'An unexpected error occurred during upload');
    }
  };

  return (
    <div className="flex flex-col min-w-0 h-dvh bg-background">
      <Header />
      <div className="flex flex-col min-w-0 gap-6 flex-1 overflow-y-scroll pt-4" ref={containerRef}>
        {messages.length === 0 && <Overview />}
        {messages.map((msg) => (
          <PreviewMessage key={msg.id} message={msg} />
        ))}
        {(isLoading || isWaitingForResponse.current) && <ThinkingMessage />}
        <div ref={endRef} className="shrink-0 min-w-[24px] min-h-[24px]" />
      </div>
      <div className="flex flex-col mx-auto px-4 bg-background pb-4 md:pb-6 gap-2 w-full md:max-w-3xl items-center">
        <div className="flex w-full gap-2">
          <ChatInput
            question={question}
            setQuestion={setQuestion}
            onSubmit={handleSubmit}
            isLoading={isLoading || isWaitingForResponse.current}
          />
        </div>
        <div className="mt-2 flex items-center gap-2">
          <input type="file" onChange={handleFileChange} className="border rounded p-2" />
          <button onClick={uploadFile} disabled={!selectedFile} className="bg-blue-500 text-white rounded p-2 hover:bg-blue-700 disabled:bg-gray-300">
            Upload File
          </button>
          {uploadStatus && <p className="text-sm text-gray-600">{uploadStatus}</p>}
        </div>
      </div>
    </div>
  );
}